import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import MenuItem from '@material-ui/core/MenuItem';
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data } = props.table;
        let art = selected!==-1?data[selected][1]:'';
        let line = selected!==-1?data[selected][2]:'';
        let material = selected!==-1?data[selected][3]:'';
        let weight = selected!==-1?data[selected][4]:'';
        let price = selected!==-1?data[selected][5]:'';
        let count = selected!==-1?data[selected][6]:'';
        let kategoty = selected!==-1?data[selected][7]:'';
        let [podkategoty, setPodkategoty] = useState(selected!==-1?data[selected][8]:'');
        let handlePodkategoty =  (event) => {
            setPodkategoty(event.target.value)
        };
        let [status, setStatus] = useState(selected!==-1?data[selected][9]:'');
        let handleStatus =  (event) => {
            setStatus(event.target.value)
        };
        const statuses = ['нет в наличие', 'в наличие', 'отключен']
        let [level, setLevel] = useState(selected!==-1?data[selected][10]:'');
        let handleLevel =  (event) => {
            setLevel(event.target.value)
        };
        const levels = ['0', '1', '2']
        let [keywords, setKeywords] = useState(selected!==-1?data[selected][11]:'');
        let handleKeywords =  (event) => {
            setKeywords(event.target.value)
        };
        let [discount, setDiscount] = useState(selected!==-1?data[selected][12]:'');
        let handleDiscount =  (event) => {
            setDiscount(event.target.value)
        };
        let cod = selected!==-1?data[selected][13]:'';
        let [file, setFile] = useState([]);
        let [fileNames, setFileNames] = useState(selected!==-1?data[selected][0]:'');
        let handleChangeFile = (async (event) => {
            setFile(event.target.files)
            let fileNames='';
            for(let i=0; i<event.target.files.length; i++){
                if(i!==0)
                    fileNames+=', '
                fileNames+=event.target.files[i].name+','
            }
            setFileNames(fileNames)
        })
        const { classes } = props;
        return (
            <div>
                <b>Артикул</b>
                <br/>
                {art}
                <br/>
                <br/>
                <b>Линейка</b>
                <br/>
                {line}
                <br/>
                <br/>
                <b>Материал</b>
                <br/>
                {material}
                <br/>
                <br/>
                <b>Вес</b>
                <br/>
                {weight}
                <br/>
                <br/>
                <b>Цена</b>
                <br/>
                {price}
                <br/>
                <br/>
                <b>Количество</b>
                <br/>
                {count}
                <br/>
                <br/>
                <b>Категория</b>
                <br/>
                {kategoty}
                <br/>
                <br/>
                <TextField
                    label='подкатегория'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={podkategoty}
                    onChange={handlePodkategoty}
                />
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='статус'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={status}
                    onChange={handleStatus}
                >
                    {
                        statuses.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='уровень'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={level}
                    onChange={handleLevel}
                >
                    {
                        levels.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <TextField
                    label='ключевые слова'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={keywords}
                    onChange={handleKeywords}
                />
                <br/>
                <TextField
                    label='скидка'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={discount}
                    onChange={handleDiscount}
                />
                <br/>
                <b>Код</b>
                <br/>
                {cod}
                <br/>
                <br/>
                <div className={classes.urls}>
                    {fileNames}
                </div>
                <br/>
                <label htmlFor='contained-button-file'>
                    <Button variant='contained' color={fileNames.length>0?'primary':''} component='span' className={classes.button}>
                        Загрузить изображение
                    </Button>
                </label>
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                         if(selected===-1)
                            addData({search: '', sort: '', page: 0, name: 'Товары', file: file, data: {podkategoria: podkategoty, status: status,  level: level, keyword: keywords, discount: discount}});
                        else
                            setData({id: data[selected][15], search: '', sort: '', page: 0, name: 'Товары', oldFile: data[selected][0], file: file, data: {podkategoria: podkategoty, status: status,  level: level, keyword: keywords, discount: discount}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
                <input
                    accept='image/*'
                    style={{ display: 'none' }}
                    id='contained-button-file'
                    multiple
                    type='file'
                    onChange={handleChangeFile}
                />
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));