import { SET_MINI_DIALOG, SHOW_MINI_DIALOG } from '../constants/mini_dialog'
import React from 'react'
import {
    store
} from '../../index'
import Ads from '../../component/Add/Ads';
import Mailing from '../../component/Add/Mailing';
import Contact from '../../component/Add/Contact';
import Blog from '../../component/Add/Blog';
import Catalog from '../../component/Add/Catalog';
import Item from '../../component/Add/Item';

export function setMiniDialog(title,child) {
    return {
        type: SET_MINI_DIALOG,
        payload: {title: title, child: child}
    }
}

export function showAddMiniDialog() {
    return async (dispatch) => {
        if(store.getState().table.name!=''){
            let child;
            if(store.getState().table.name=='Реклама')
                child = <Ads/>
            else if(store.getState().table.name=='Блог')
                child = <Blog/>
            else if(store.getState().table.name=='Рассылка')
                child = <Mailing/>
            else if(store.getState().table.name=='Товары')
                child = <Item/>
            else if(store.getState().table.name=='Контакты')
                child = <Contact/>
            else if(store.getState().table.name=='Каталог')
                child = <Catalog/>
            dispatch ({
                type: SET_MINI_DIALOG,
                payload: {title: store.getState().table.name, child: child}
            })
            dispatch ({
                type: SHOW_MINI_DIALOG,
                payload: true
            })
        }
    }
}

export function showMiniDialog(show) {
    return {
        type: SHOW_MINI_DIALOG,
        payload: show
    }
}
