import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as mini_dialogActions from '../../redux/actions/mini_dialog'
import * as tableActions from '../../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth>800? 500: (mainWindow.current.offsetWidth-144);

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
});

const Sign =  React.memo(
    (props) =>{
        const { showMiniDialog } = props.mini_dialogActions;
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data } = props.table;

        let [mailuser, setMailuser] = useState(selected!==-1?data[selected][0]:'');
        let handleMailuser =  (event) => {
            setMailuser(event.target.value)
        };
        let [mailpass, setMailpass] = useState(selected!==-1?data[selected][1]:'');
        let handleMailpass =  (event) => {
            setMailpass(event.target.value)
        };
        let [mailchimpInstance, setMailchimpInstance] = useState(selected!==-1?data[selected][2]:'');
        let handleMailchimpInstance =  (event) => {
            setMailchimpInstance(event.target.value)
        };
        let [listUniqueId, setListUniqueId] = useState(selected!==-1?data[selected][3]:'');
        let handleListUniqueId =  (event) => {
            setListUniqueId(event.target.value)
        };
        let [mailchimpApiKey, setMailchimpApiKey] = useState(selected!==-1?data[selected][4]:'');
        let handleMailchimpApiKey =  (event) => {
            setMailchimpApiKey(event.target.value)
        };
        const { classes } = props;
        return (
            <div>
                <TextField
                    label='mailuser'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={mailuser}
                    onChange={handleMailuser}
                />
                <br/>
                <TextField
                    label='mailpass'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={mailpass}
                    onChange={handleMailpass}
                />
                <br/>
                <TextField
                    label='mailchimpInstance'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={mailchimpInstance}
                    onChange={handleMailchimpInstance}
                />
                <br/>
                <TextField
                    label='listUniqueId'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={listUniqueId}
                    onChange={handleListUniqueId}
                />
                <br/>
                <TextField
                    label='mailchimpApiKey'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={mailchimpApiKey}
                    onChange={handleMailchimpApiKey}
                />
                <br/>
                <div>
                    <Button variant='contained' color='primary' onClick={()=>{
                        if(selected===-1)
                            addData({search: '', sort: '', page: 0, name: 'Рассылка', data: {mailuser: mailuser, mailpass: mailpass, mailchimpInstance: mailchimpInstance, listUniqueId: listUniqueId, mailchimpApiKey: mailchimpApiKey}});
                        else
                            setData({id: data[selected][6], search: '', sort: '', page: 0, name: 'Рассылка', data: {mailuser: mailuser, mailpass: mailpass, mailchimpInstance: mailchimpInstance, listUniqueId: listUniqueId, mailchimpApiKey: mailchimpApiKey}});
                        setSelected(-1)
                        showMiniDialog(false)}} className={classes.button}>
                        Сохранить
                    </Button>
                    <Button variant='contained' color='secondary' onClick={()=>{setSelected(-1); showMiniDialog(false)}} className={classes.button}>
                        Отмена
                    </Button>
                </div>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        mini_dialog: state.mini_dialog,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        mini_dialogActions: bindActionCreators(mini_dialogActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Sign.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Sign));